import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss']
})
export class LoginFormComponent implements OnInit {
  @Output() sendLoginForm = new EventEmitter<void>();
  constructor(
    public helpSnackBar: MatSnackBar,
  ) { }
  public form: FormGroup;
  public flatlogicAccount = '';
  public flatlogicPassword = '';

  pageControl = {
    'hidePassword' : true
  }
  accountPlaceholder:string = "帳號";
  passwordPlaceholder:string = "密碼";
  public ngOnInit(): void {
    this.form = new FormGroup({
      account: new FormControl(this.flatlogicAccount, [Validators.required]),
      password: new FormControl(this.flatlogicPassword, [Validators.required])
    });
  }

  public login(): void {
    if (this.form.valid) {
      this.sendLoginForm.emit(this.form.value);
    } else {
      this.openHelp();
    }
  }

  checkPlaceholder(value) {
    if(value == 'account') {
      if(this.accountPlaceholder) {
        this.accountPlaceholder = null
      } else {
        this.accountPlaceholder = "帳號";
      }
    } else {
      if(this.passwordPlaceholder) {
        this.passwordPlaceholder = null
      } else {
        this.passwordPlaceholder = "密碼";
      }
    }
  }
  //提示
  openHelp() {
    this.helpSnackBar.open('有欄位尚未填寫', '了解', {
      duration: 1000,
      horizontalPosition: 'center',
      verticalPosition: 'top',
    });
  }
}
