import { Component, EventEmitter, Input, Output, OnInit, OnDestroy } from '@angular/core';

import { Email, User } from '../../pages/auth/models';
import { AuthService, EmailService } from '../../pages/auth/services';
import { Router, ActivatedRoute, RouterModule } from "@angular/router";
// rxjs調用
import { Observable, ReplaySubject } from 'rxjs';

import { routes } from '../../routes';
// centerService
import { CenterService } from '../../center.service'
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {
  @Input() isMenuOpened: boolean;
  @Output() isShowSidebar = new EventEmitter<boolean>();
  public user$: Observable<User>
  public emails$: Observable<Email[]>
  public routers: typeof routes = routes;
  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

  // header右上分頁
  pagination = []
  paginationDict = {}

  phoneMode = false// 是否為手機模式
  paginationOpen = false

  autoMode = 'false' // 自動播放模式

  constructor(
    private centerService: CenterService,
    private userService: AuthService,
    private emailService: EmailService,
    private router: Router,
    private route: ActivatedRoute,
  ) {
    this.user$ = this.userService.getUser();
    this.emails$ = this.emailService.loadEmails();

  }

  ngOnInit(): void {

  }
  logout() {
    this.centerService.post('/api/users/logout',
      {}).subscribe();
    // 跳轉到登入頁面
    this.router.navigate([this.routers.LOGIN]).then();
  }
  public ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }
}
