import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment';

import { AuthService } from './services';
import { routes } from '../../routes';

// centerService
import { CenterService } from '../../center.service';

@Component({
  selector: 'app-auth-page',
  templateUrl: './auth-page.component.html',
  styleUrls: ['./auth-page.component.scss'],
})
export class AuthPageComponent implements OnInit {
  public todayDate: Date = new Date();
  public routers: typeof routes = routes;

  pageControl = {
    AnimationChange: true,
    night: false,
    Timer: 0,
    timeLimit: 30,
  };

  constructor(
    private service: AuthService,
    private router: Router,
    private centerService: CenterService
  ) {}

  @HostListener('document:mousemove', ['$event'])
  onMouseMove() {
    if (!this.pageControl.night) {
      this.pageControl.Timer = 0;
      if (this.pageControl.AnimationChange === false) {
        this.pageControl.AnimationChange = true;
      }
    }
  }

  ngOnInit(): void {
    //loading先關閉
    this.centerService.loading$.next(false);
    setInterval(() => {
      if (this.pageControl.AnimationChange === true) {
        if (this.pageControl.Timer > this.pageControl.timeLimit) {
          this.pageControl.AnimationChange = false;
        }
        this.pageControl.Timer += 1;
      }
    }, 1000);
    const hr = parseInt(moment().format('HH'));
    if (hr < 7 || hr > 18) {
      this.pageControl.night = true;
    } else {
      this.pageControl.night = false;
    }
  }

  public sendLoginForm(e): void {
    this.service.login(e);

    // this.router.navigate([this.routers.QUERY]).then();
  }

  public sendSignForm(): void {
    this.service.sign();

    this.router.navigate([this.routers.QUERY]).then();
  }
}
