import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../environments/environment';
import { Observable, Subject } from 'rxjs';

const config = {
  'serverIP': environment.serverIP,
  'reqHeader': new HttpHeaders({
    'Content-Type': 'application/json',
    'access-control-allow-origin': '*',
    // 'authorization': 'Bearer ' + localStorage.getItem('token') //JWT放這裡
  }),
};


interface sd {
  hour: string[];
  id: string[];
  holidays: string[];
}
interface sd_district {
  hour: string[];
  district: number[];
  holidays: string[];
}
interface offstreet {
  start: string;
  end: string;
  id: string[];
  hour: string[];
  holidays: string[];
  revenue: string;
}
interface onstreet {
  start: string;
  end: string;
  id: string[];
  hour: string[];
  holidays: string[];
}
interface total_id {
  sd: number;
  on_street: number;
  off_street: number;
}
interface mutli {
  start: string;
  end: string;
  district: number[];
  hour: string[];
  holidays: string[];
  is_day: string;
  revenue: string;
}
interface mutli_id {
  start: string;
  end: string;
  id: string[];
  hour: string[];
  holidays: string[];
  is_day: string;
  revenue: string;
}
@Injectable({
  providedIn: 'root'
})
export class CenterService {

  constructor(
    private http: HttpClient,
  ) { }

  /*==================================
    Subject
  ==================================*/
  // 彈出讀取屏蔽
  loading$ = new Subject<boolean>();
  // 搜尋時轉拋給各components
  fillter_sd_id$ = new Subject<sd>();
  fillter_sd_district$ = new Subject<sd_district>();
  fillter_multi$ = new Subject<mutli>();
  fillter_multi_id$ = new Subject<mutli_id>();
  fillter_offStreet$ = new Subject<offstreet>();
  fillter_onStreet$ = new Subject<onstreet>();
  fillter_onStreet_days$ = new Subject<onstreet>();
  // 紀錄各來源有多少路段
  total_id$ = new Subject<total_id>();
  // 進入全螢幕地圖框選 -1:清空, -99:關閉
  mapSelectType$ = new Subject<number>();
  // 框選的id回傳
  returnMapSelectId$ = new Subject<string[]>();
  // 框選的停車場基本資料建議資訊回傳
  returnMapSelectParking$ = new Subject<number[]>();
  //地圖點位在入完畢時回傳
  returnReady$ = new Subject<string>();
  //停車推估按鈕 1:查詢, 2:重新推估
  estimate_btn$ = new Subject<number>();
  // 推估結果
  estimate_result$ = new Subject<number[]>();

  //google map 地址搜尋
  address_search$ = new Subject<string>();

  // get方法
  public get(url, params = {}): Observable<any> {
    const URL = config.serverIP + url;
    return this.http.get<any>(URL, { headers: config.reqHeader, params: params });
  }

  // post方法
  public post(url, data): Observable<any> {
    const URL = config.serverIP + url;
    return this.http.post<any>(URL, data, { headers: config.reqHeader, withCredentials: true });
  }

}


