import { ChangeDetectorRef, Component, OnInit, OnDestroy, ViewChild, enableProdMode } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';
import { MatSidenav } from '@angular/material/sidenav';

//rxjs
import { filter, takeUntil } from 'rxjs/operators';
import { ReplaySubject } from 'rxjs';
import { NavigationEnd, Router } from '@angular/router';
// centerService
import { CenterService } from '../../center.service'


@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss'],
})
export class LayoutComponent implements OnInit, OnDestroy {
  @ViewChild('sidenav') sidenav: MatSidenav;
  public isShowSidebar: boolean;
  public mobileQuery: MediaQueryList;
  private mobileQueryListener: () => void;

  constructor(
    private centerService: CenterService,
    changeDetectorRef: ChangeDetectorRef,
    private router: Router,
    media: MediaMatcher,
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 800px)');
    this.mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this.mobileQueryListener);

    // this.isShowSidebar = !this.mobileQuery.matches;
    this.isShowSidebar = false;

    // 取得第一層分頁data
    router.events.pipe(
      filter(e => e instanceof NavigationEnd),
      takeUntil(this.destroyed$)
    ).subscribe(() => {
      if (this.router.url.split('/').pop() === ''|| this.router.url.split('/').pop() === 'login') {
        this.loginPage = true;
      } else {
        this.loginPage = false;

      }
    });
  }

  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  public mapControl: string = 'main-map'
  public menu: boolean = false
  loginPage :boolean = true;
  loading = false;

  ngOnInit(): void {
    // 是否開啟loading
    this.centerService.loading$.pipe(
      takeUntil(this.destroyed$)
    ).subscribe(d=>this.loading = d)
    // 視窗大小決定要不要開menu
    if (document.documentElement.clientWidth <= 800) {
      this.menu = false
    }
    window.addEventListener('resize', ()=>{
      let clientWidth = document.documentElement.clientWidth;
      if (clientWidth <= 800) {
        this.menu = false
      } else {
        this.menu = false
      }
    });
  }

  public ngOnDestroy(): void {
    this.mobileQuery.removeListener(this.mobileQueryListener);

    this.sidenav.close();

    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

}
